import React, { Component, PropTypes } from 'react';

class InputCell extends Component {
  constructor(props) {
    super(props);

    this.state ={
      value: ''
    }

    this.handleChange = this.handleChange.bind(this)
  }

  componentWillMount() {
    this.setState({
      value: this.props.arr
    })
  }

  handleChange(event) {
     this.setState({value: event.target.value});
     this.props.handleChildChange(event.target.value);
   }

  render() {
    return (
      <div>
          <input className="box" value={this.state.value} onChange={this.handleChange} />
     </div>
    );
  }
}

export default InputCell;



